import { useDispatch } from "react-redux";
import { pressCalendarItem } from "../../redux/actions";

const RenderItem = ({ place }) => {
  const { isReserved, isMarked, price } = place;
  const dispatch = useDispatch();

  let className = "";
  if (isReserved === "false") {
    className = "item-disable";
  } else {
    className = "item";
  }

  if (isMarked) {
    className = "item-activ item-price-activ";
  }

  return (
    <div
      onClick={() => dispatch(pressCalendarItem(place))}
      className={className}
    >
      <div className={"item-price"}>{`${price} грн`}</div>
    </div>
  );
};

export default RenderItem;
