import * as reservationActions from "./actions";

const initialState = {
  tableData: [],
  reservations: [],
  calendarData: "",
  orderSum: 0,
  isCartPress: false,
};

const BALL_PRICE = 50;

export const reservationReducer = (state = initialState, action) => {
  switch (action.type) {
    case reservationActions.SET_TABLE_DATA:
      return {
        ...state,
        tableData: action.tableData,
        calendarData: action.calendarData,
      };

    case reservationActions.SET_INITIAL_STATE:
      return initialState;

    case reservationActions.ADD_AND_REMOVE_FROM_RESERVATION:
      const { id, isMarked, isReserved, isBall } = action.item;

      if (isReserved === "true") {
        return state;
      }
      // must Refactoring
      const newTableData = state.tableData.map((el) => {
        if (el.place1.id === id) {
          el.place1.isMarked = !el.place1.isMarked;
          el.place1.isBall = true;
        }
        if (el.place2.id === id) {
          el.place2.isMarked = !el.place2.isMarked;
          el.place2.isBall = true;
        }
        if (el.place3.id === id) {
          el.place3.isMarked = !el.place3.isMarked;
          el.place3.isBall = true;
        }
        if (el.place4.id === id) {
          el.place4.isMarked = !el.place4.isMarked;
          el.place4.isBall = true;
        }
        if (el.place5.id === id) {
          el.place5.isMarked = !el.place5.isMarked;
          el.place5.isBall = true;
        }
        if (el.place6.id === id) {
          el.place6.isMarked = !el.place6.isMarked;
          el.place6.isBall = true;
        }
        return el;
      });
      // ////////////////////////////////////////////////////////////////////////////////

      const reservatinItem = {
        userId: window.location.href.split('=')[1],
        item: action.item,
      };

      let sum;
      if (isBall && !isMarked) {
        sum = state.orderSum + Number(reservatinItem.item.price) + BALL_PRICE;
      } else {
        if (isBall) {
          sum = state.orderSum - Number(reservatinItem.item.price) - BALL_PRICE;
        } else {
          sum = state.orderSum - Number(reservatinItem.item.price);
        }
      }

      return {
        ...state,
        tableData: newTableData,
        reservations: isMarked
          ? state.reservations.filter((item) => item.item.id !== id)
          : [...state.reservations, reservatinItem],
        orderSum: sum,
      };

    case reservationActions.TOGGLE_BALL:
      const newTable = state.tableData.map((el) => {
        if (el.place1.id === action.id) {
          el.place1.isBall = !el.place1.isBall;
        }
        if (el.place2.id === action.id) {
          el.place2.isBall = !el.place2.isBall;
        }
        if (el.place3.id === action.id) {
          el.place3.isBall = !el.place3.isBall;
        }
        if (el.place4.id === action.id) {
          el.place4.isBall = !el.place4.isBall;
        }
        if (el.place5.id === action.id) {
          el.place5.isBall = !el.place5.isBall;
        }
        if (el.place6.id === action.id) {
          el.place6.isBall = !el.place6.isBall;
        }
        return el;
      });

      return {
        ...state,
        tableData: newTable,
        orderSum: action.isBall
          ? state.orderSum - BALL_PRICE
          : state.orderSum + BALL_PRICE,
      };

    case reservationActions.CART_PRESS:
      return {
        ...state,
        isCartPress: !state.isCartPress
      };
    default:
      return state;
  }
};
