import React from "react";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import { useSelector } from "react-redux";
import Checkbox from "./Checkbox";
import RemoveButton from "./Remove-button";

const CustomList = (props) => {
  const { reservations, calendarData } = useSelector(
    (state) => state.reservations
  );
  const sortReservations = reservations.sort((a, b) => {
    if (a.item.cord === b.item.cord) {
      return Number(a.item.time.split(":")[0]) >
        Number(b.item.time.split(":")[0])
        ? 1
        : -1;
    } else {
      return a.item.cord > b.item.cord ? 1 : -1;
    }
  });

  return (
    <List style={{ marginTop: "3.1rem" }}>
      {sortReservations.map((el, index) => {
        const cordName = el.item.cord;
        const price = el.item.price;
        const time = el.item.time;
        return (
          <div key={index}>
            <div
              style={{
                paddingLeft: "10px",
                paddingRight: "10px",
                cursor: "default",
              }}
            >
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
              >
                <p style={{ fontSize: "1rem" }}>{cordName}</p>
                <p style={{ color: "grey" }}>{`${calendarData.year}/${calendarData.month}/${calendarData.day}`}</p>
                <p style={{ color: "#EC1547" }}>{`${time}`}</p>
                <p style={{ color: "grey" }}>{`ціна ${price} грн`}</p>
                <RemoveButton item={el.item} />
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
              >
                
                </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <p style={{ color: "grey", fontSize: "10px" }}>
                  Ви можете відмовитись від м'яча та взяти свій *
                </p>
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <Checkbox item={el.item} />
                  <p style={{ fontWeight: "bold", fontSize: "10px" }}>
                    М'яч 50 грн
                  </p>
                </div>
              </div>
            </div>
            <Divider />
          </div>
        );
      })}
    </List>
  );
};

export default CustomList;
