import { changeData } from "../tableAPI/changeData";
// import { tableData } from "../tableAPI/testDataTable";

export const BASE_URL = "https://hraboviy.pp.ua";
export const SET_TABLE_DATA = "SET_TABLE_DATA";
export const SET_INITIAL_STATE = "SET_INITIAL_STATE";
export const ADD_AND_REMOVE_FROM_RESERVATION =
  "ADD_AND_REMOVE_FROM_RESERVATION";
export const SET_USER_ID = "SET_USER_ID";
export const TOGGLE_BALL = "TOGGLE_BALL";
export const REMOVE_RESERVATIONS_AND_SUM = "REMOVE_RESERVATIONS";
export const DELETE_ITEM_IN_RESERVATIONS = "DELETE_ITEM_IN_RESERVATIONS";
export const CART_PRESS = "CART_PRESS";

export const fetchTableData = (selectedDate) => {
  return async (dispatch) => {
    let month = selectedDate.month;
    if (selectedDate.month <= 9) {
      month = `0${month}`;
    }
    console.log('ОТПРАВИЛ ДАННИЕ');
    const url = `?q=${selectedDate.year}-${month}-${selectedDate.day}`;
    const response = await fetch(`${BASE_URL}/${url}`, { method: "GET" });
    console.log('ПОЛУЧИЛ ДАННИЕ');
    if (!response.ok) {
      const errorResData = await response.json();
      throw new Error(errorResData);
    }
    const resData = await response.json();

    const newData = changeData(resData);

    dispatch({
      type: SET_TABLE_DATA,
      tableData: newData,
      calendarData: selectedDate,
    });
  };
};

export const pressCalendarInput = () => {
  return {
    type: SET_INITIAL_STATE,
  };
};

export const pressCalendarItem = (item) => {
  return (dispatch) => {
    dispatch({
      type: ADD_AND_REMOVE_FROM_RESERVATION,
      item,
    });
  };
};

export const toggleBall = (id, isBall) => {
  return {
    type: TOGGLE_BALL,
    id,
    isBall,
  };
};

export const cartPress = () => {
  return {
    type: CART_PRESS,
  };
};
