import React, { useState } from "react";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import IconButton from "@material-ui/core/IconButton";
import Badge from "@material-ui/core/Badge";
import { useDispatch, useSelector } from "react-redux";
import { cartPress } from "../../redux/actions";
import { Slide } from "@material-ui/core";

const HeaderCart = (props) => {
  const [isPressCart, setIsPressCart] = useState(false);
  const reservations = useSelector((state) => state.reservations.reservations);
  const dispatch = useDispatch();

  const cartPressHandler = () => {
    dispatch(cartPress());

    setIsPressCart(true);

    setTimeout(() => {
      setIsPressCart(false);
    }, 1000);
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        position: "absolute",
        right: "10px",
      }}
    >
      <IconButton
        edge="start"
        color="inherit"
        onClick={cartPressHandler}
        aria-label="close"
        style={{
          marginLeft: "20px",
          marginTop: "10px"
        }}
      >
        <Badge badgeContent={reservations.length} color="secondary">
          <ShoppingCartIcon color="inherit" />
        </Badge>
      </IconButton>
      <Slide
        direction="left"
        timeout={{
          enter: 500,
          exit: 1,
        }}
        in={reservations.length === 0 && isPressCart ? true : false}
      >
        <div style={{top: '2.9rem',  position: "absolute", right: '-.3rem', display: 'flex', fontSize: '0.5rem'}} {...props}>
            кошик порожній
        </div>
      </Slide>
    </div>
  );
};

export default HeaderCart;
