import { useDispatch, useSelector } from "react-redux";
import * as reservationActions from "../../redux/actions";

const CustomInput = ({ ref }) => {
  const calendarData = useSelector((state) => state.reservations.calendarData);
  const dispatch = useDispatch();

  return (
    <input
      onClick={() => dispatch(reservationActions.pressCalendarInput())}
      readOnly
      ref={ref}
      placeholder="Виберіть дату бронювання"
      size={25}
      value={
        calendarData
          ? `✅ ${calendarData.year}-${calendarData.month}-${calendarData.day} `
          : ""
      }
      style={{
        marginTop: "5.5rem",
        textAlign: "center",
        padding: "1rem 1rem",
        fontWeight: "bold",
        fontSize: "1rem",
        border: "1px solid white",
        borderRadius: "100px",
        color: "white",
        outline: "none",
        cursor: "pointer",
        backdropFilter: "blur(10px",
        backgroundColor: "rgba(0,0,0,0.3)",
      }}
      className="my-custom-input-class"
    />
  );
};

export default CustomInput;
