import Button from "@material-ui/core/Button";
import { Slide } from "@material-ui/core";
import { useSelector } from "react-redux";

const Btn = (props) => {
  const { reservations } = useSelector((state) => state.reservations);

  return (
    <Slide
      direction="up"
      timeout={{
        enter: 500
      }}
      in={reservations.length !== 0 ? true : false}
    >
      <div {...props}>
        <Button
          variant="contained"
          color="primary"
        >
          {props.children}
        </Button>
      </div>
    </Slide>
  );
};

export default Btn;
