import React from "react";
import Button from "@material-ui/core/Button";
import { useDispatch, useSelector } from "react-redux";
import { pressCalendarInput } from "../../redux/actions";

const PayButton = (props) => {
  const { reservations, orderSum, calendarData } = useSelector(
    (state) => state.reservations
  );
  const dispatch = useDispatch();

  const handlePay = () => {
    let month = calendarData.month;
    if (calendarData.month <= 9) {
      month = `0${month}`;
    }
    const dataObj = {
      calendarData: `${calendarData.year}-${month}-${calendarData.day}`,
      orderSum,
      reservations,
    };
    const jsonData = JSON.stringify(dataObj);
    const url = `https://hraboviy.pp.ua/api-test/`;
    fetch(`${url}`, { 
      method: "POST",
      body: jsonData
    });
    dispatch(pressCalendarInput());
  };

  return (
    <Button autoFocus color="inherit" onClick={handlePay}>
      СПЛАТИТИ
    </Button>
  );
};

export default PayButton;
